.chargeTile{
  word-break: word-break !important;  
  padding-right: 15%;
}
.tileicon{
  font-size: 60px !important;
   color: white; 
   width: 60px;
} 
@media(max-width:1200px){
  .tileicon{
    font-size: 30px !important;
     color: white; 
     width: 30px;
  } 
  .pos {
    position: relative;
    left: 1rem;
    bottom: 20px; 
    color: whitesmoke;
  }
  
  .pos2 { 
    left:1rem;
    bottom: 20px;
    color: whitesmoke;
  }
  
  .pos3 {
    position: relative;
    left: 1rem;
    bottom: 20px;
    color: whitesmoke;
  }
  
}
